@import url(https://fonts.googleapis.com/css?family=Roboto:900);
@import url(https://fonts.googleapis.com/css?family=Open + Sans);
@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.HomePage {
  margin: 10px 50px 10px 50px;
}

.HomePage p {
  text-indent: 50px;
  text-align: justify;
  font-style: italic;
}

.AuthorEmail {
  color: var(--color-app);
}

.Error {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-size: 25px;
  font-family: 'Open Sans', sans-serif;
  height: inherit;
  justify-content: center;
  text-align: center;
  width: inherit;
}

.Error-Message {
  color: var(--font-color-app);
  text-transform: capitalize;
  margin: 25px 0 20px 0;
}

.Error-Label span {
  position: relative;
  background: var(--color-app);
  color: var(--font-color-app);
  font-size: 300%;
  padding: 0 20px;
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  font-weight: bolder;
  transition: all 0.5s;
  cursor: pointer;
}

.Error-Label span:hover {
  background: var(--dark-color-app);
  color: var(--font-color-app);
  -webkit-animation: jelly 0.5s;
  animation: jelly 0.5s;
}

.Loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: var(--font-color-app);
  letter-spacing: 1rem;
  font-size: 120%;
  font-family: Lato, sans-serif;
  text-transform: uppercase;
}

.Loader-Span {
  -webkit-animation: blur 1.5s alternate infinite;
          animation: blur 1.5s alternate infinite;
}

@-webkit-keyframes blur {
  to {
    -webkit-filter: blur(5px);
            filter: blur(5px);
  }
}

@keyframes blur {
  to {
    -webkit-filter: blur(5px);
            filter: blur(5px);
  }
}

.PageNotFound {
  height: inherit;
  width: inherit;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.NavBarHeader {
  background-color: inherit;
  color: var(--font-color-app);
  white-space: nowrap;
}

.NavBarHeader-Title {
  align-items: center;
  font-size: 1em;
  justify-content: center;
  line-height: 20px;
  padding: 20px 0;
  text-align: left;
}

.NavBar-Icon {
  font-size: 2em;
  vertical-align: middle;
}

.sidenav---sidenav---_2tBP {
  background: var(--sidenav-background-app);
}

.MainComponent {
  color: white;
  flex-grow: 2;
  height: 100vh;
  overflow: auto;
  padding: 10px 15px 10px 15px;
  margin-top: 0;
}

.Container {
  display: flex;
  height: 100vh;
  overflow: hidden;
  width: 100%;
}

:root {
  --font-color-app: #fff;
  --background-color: #282c34;
  --color-app: #e93d44;
  --dark-color-app: #9f131a;
  /* --background-color: #1d1e20;
  --color-app: #3d71e0;
  --dark-color-app: #255cd4; */
  --sidenav-background-app: var(--dark-color-app);

  --hover-item-bg-color: #fff;
  --default-item-bg-color: var(--background-color); /*#000;*/
  --selected-item-bg-color: var(--dark-color-app); /*#006341;*/
  --filtered-item-bg-color: var(--dark-color-app);

  --hover-item-font-color: #000;
  --default-item-font-color: #fff; /*var(--dark-color-app);*/
  --selected-item-font-color: #fff;
  --filtered-item-font-color: #fff;
}

.App {
  align-items: center;
  background-color: #282c34;
  background-color: var(--background-color);
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

h2 {
  text-align: center;
  color: #fff;
  color: var(--font-color-app);
  text-shadow: 1px 2px 2px #e93d44;
  text-shadow: 1px 2px 2px var(--color-app);
  margin: 15px 15px 15px 15px;
}

h4,
label {
  text-align: center;
  vertical-align: middle;
  color: #fff;
  color: var(--font-color-app);
  text-shadow: 1px 2px 2px #e93d44;
  text-shadow: 1px 2px 2px var(--color-app);
  margin: 15px 15px 15px 15px;
}

@-webkit-keyframes shine {
  0% {
    transform: translateX(-100%) translateY(-100%) rotate(30deg);
  }
  80% {
    transform: translateX(-100%) translateY(-100%) rotate(30deg);
  }
  100% {
    transform: translateX(100%) translateY(100%) rotate(30deg);
  }
}

@keyframes shine {
  0% {
    transform: translateX(-100%) translateY(-100%) rotate(30deg);
  }
  80% {
    transform: translateX(-100%) translateY(-100%) rotate(30deg);
  }
  100% {
    transform: translateX(100%) translateY(100%) rotate(30deg);
  }
}

.MexOptionsPanel {
  border-radius: 20px;
  border: 1px solid #282c34;
  border: 1px solid var(--background-color);
  /* background-color: var(--selected-item-bg-color); */
  /*   
  &:hover {
    transform: translate3d(0px, 0px, -250px);
  } */
}

.YearChoice {
  background-color: inherit;
  border: 2px solid #555555;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 5px;
  padding: 12px 30px;
}

.Filtered {
  background-color: #9f131a;
  background-color: var(--filtered-item-bg-color);
  color: var(--filtered-item-fc-color);
}

.Selected {
  background-color: #9f131a;
  background-color: var(--selected-item-bg-color);
  color: var(--selected-item-fc-color);
}

.MissingItem {
  background-color: #000;
  color: var(--selected-item-fc-color);
  cursor: default;
  font-size: 14px;
  padding: 10px 20px;
}

